<template>
    <div class="index" v-if="data">
        <header>
            <van-uploader    :after-read="afterRead" >
                <img :src="data.cover_image" >
            </van-uploader>
            <div class="icon-left-box" @click="handleGoBack" >
                <!-- <img class="icon-left"  src="../../assets/images/left.png" > -->
                <van-icon name="arrow-left" color="#ffffff" />
            </div>
           
        </header>
        <section class="content">
            <div class="avatar-box">
                <img :src="data.avatar" >
                <img class="isvip" src="../../assets/images/icon_isvip.png"  v-if="data.is_ident_icon == 1" >
            </div>
            <div class="info flex justify-space-between">
                <div class="left">
                    <p class="username" @click="handleToeditUsername">{{data.username}}</p>
                    <div class="flex flex-none"><p class="level" v-bind:class="{'isCarvip':data.is_carvip == 1}">{{data.level}}</p><p class="extcredits">{{data.extcredits2}}°</p></div>
                </div>
                <div class="right">
                    <div class="edit" @click="handleToeditUserinfo">编辑资料</div>
                    <router-link to="/my/friendlist" tag="div" class="friend"> 
                      
                    </router-link>
             
                    <div class="share"></div>
                </div>
            </div>
            <p class="bio" @click="handleToEditsignature">{{data.bio? data.bio : '填写个人签名更容易获得别人关注哦~'}}</p>
            <p class="label"><span class="gender">{{data.gender}}</span><span class="residecity" v-if="data.residecity">{{data.residecity}}</span><span class="add-label">+增加标签</span></p>
            <div class="attest" v-if="tabs == 4 && appleType != 0 " @click="handleAttest" >认证</div>
            <div class="flex  count">
                <div><p class="item">获赞</p><p class="value" @click="handleToPage(1)">{{data.zan}}</p></div>
                <div><p class="item">被踩</p><p class="value" @click="handleToPage(2)">{{data.cai}}</p></div>
                <div><p class="item">关注</p><p class="value" @click="handleToPage(3)">{{data.following}}</p></div>
                <div><p class="item">粉丝</p><p class="value" @click="handleToPage(4)">{{data.follower}}</p></div>
            </div>
        </section>
        <van-divider />
        <div class="tabs">
            <ul class="flex">
                <li v-bind:class="{'active':tabs == 3}" @click="handleTabs(3)">广场</li>
                <li v-bind:class="{'active':tabs == 2}" @click="handleTabs(2)">问答</li>
                <li v-bind:class="{'active':tabs == 1}" @click="handleTabs(1)">投票</li>
                <li v-bind:class="{'active':tabs == 4}" @click="handleTabs(4)">更多</li>
            </ul>
        </div>
        <div>
            <router-view :uid="data.uid" :type='tabs'> </router-view>
        </div>
    </div>
</template>

<script>
import { Divider } from 'vant';
import { Toast } from 'vant';
export default {
    data (){
        return {
            data:'',
            appleType:0, // 认证类型
            tabs:this.$route.query.tabs?this.$route.query.tabs:3,
        }
    },
    created(){
            document.title = '我的';
            if(this.$store.state.userData){
                this.getUserInfo();
                this.getApplyType();
            }else {
                window.location.href= "teldapp://login"
            }   

    },
    mounted(){
        this.$nextTick(()=>{

        })
    },
    methods:{
        handleTabs(val){
            this.tabs = val;
            
            if(val == 3){
                this.$router.push({ path: '/my/index/square',query:{tabs:val}})
            }else if(val == 2){
                this.$router.push({ path: '/my/index/answers',query:{tabs:val}})
            }else if(val == 1){
                this.$router.push({ path: '/my/index/vote',query:{tabs:val}})
            }else if(val == 4){
                this.$router.push({ path: '/my/index/more',query:{tabs:val}})
            }
        },
        afterRead(file){
           this.addImageOfUserCover(file.file);
        },   
        // 点击认证按钮
        handleAttest(){

            switch(this.appleType){
                case 1:
                    this.$router.push({ path: `/my/mycertification` })
                    break;
            }
        },
        // 获取认证类别
        getApplyType(){
            this.api.getApplyType({
                token:this.$store.state.userData
            }).then(res =>{
                if(res.err_code == 0){
                    this.appleType = res.data.type
                }else {

                }
            })
        },
        // 上传用户封面图
        addImageOfUserCover(file){
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });
            let formData = new FormData();
            formData.append('token',this.$store.state.userData)
            formData.append('myfile',file)
            this.api.addImageOfUserCover(formData).then(res => {
                Toast.clear();
                if(res.err_code == 0){
                    this.getUserInfo()
                }else {
                  
                }
            }).catch(err => {
                Toast(err)
            })
        },
        // 编辑签名
        handleToEditsignature(){
            this.$router.push({ path: '/my/editsignature' })
        },
        // 编辑昵称
        handleToeditUsername(){
            this.$router.push({ path: '/my/editusername' })
        },
        // 编辑资料
        handleToeditUserinfo(){
            this.$router.push({ path: '/my/edituserinfo' })
        },
        // 添加页面
        handleToPage(v){
            switch(v){
                case 1:
                    break;
                case 2:
                    break;
                case 3:
                        this.$router.push({ path: '/my/attention' ,query:{type:1,user:1}})
                    break;
                case 4:
                        this.$router.push({ path: '/my/attention' ,query:{type:2,user:1}})
                    break;            
            }
        },
        handleGoBack(){
            this.$router.push({path:'/'})
        },
        // 获取 用户信息
        getUserInfo(){
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });    
            this.api.getUserInfo({
                token:this.$store.state.userData,
                uid:''
            }).then( res => {
                Toast.clear();
                if(res.err_code == 0){
                    this.data = res.data;
                   
                }
            })
        },
    }
}
</script>

